$accent-color: #f5da46;
$main-grey: #888888;
$light-grey: #e8e8e8;

.calculator {
  max-width: 400px;
  &__tabs {
    display: flex;
    justify-content: space-between;
    position: relative;
    &::after {
      content: '';
      display: block;
      width: 100%;
      height: 2px;
      position: absolute;
      left: 0;
      bottom: 0;
      background-color: $accent-color;
    }
  }
  &__tab {
    position: relative;
    & > input {
      position: absolute;
      left: 20%;
      z-index: -10;
      visibility: hidden;
    }
    & > label {
      z-index: 10;
      padding: 10px 25px;
      display: block;
      border-radius: 10px 10px 0px 0px;
      cursor: pointer;
      transition: .2s;
      font-size: 1.8rem;
    }
    & > input:checked + label {
      background-color: $accent-color;
    }
  }
  &__main {
    padding-top: 25px;
    padding-bottom: 25px;
  }
  &__row {
    margin-bottom: 50px;
  }
  &__text-row {
    display: flex;
    justify-content: space-between;
    margin-bottom: 16px;
  }
  &__text {
    font-size: 1.4rem;
  }
  &__result {
    font-size: 1.7rem;
    font-weight: 700;
  }
  &__slider {

  }
  &__total {
    margin-bottom: 15px;
  }
  &__btn {
    background-color: $accent-color;
    border-radius: 5px;
    text-align: center;
    border: 2px solid $accent-color;
    transition: .2s;
    &:hover {
      background-color: #ffffff;
    }
    & > a {
      padding: 15px;
      display: inline-block;
      width: 100%;
    }
  }
}

.calcslider {
  &__line {
    width: 100%;
    height: 5px;
    background-color: $light-grey;
    border-radius: 2.5px;
    position: relative;
  }
  &__pointer {
    width: 13px;
    height: 13px;
    background-color: $accent-color;
    border: 1px solid $accent-color;
    border-radius: 50%;
    position: absolute;
    left: 0;
    top: -4px;
    cursor: pointer;
  }
  &__min,
  &__max {
    margin-top: 20px;
    font-size: 1.2rem;
    color: #c3c3c3;
  }
  &__min {
    float: left;
  }
  &__max {
    float: right;
  }
}

.calctotal {
  display: flex;
  justify-content: space-between;
  background-color: #000000;
  border-radius: 5px;
  padding: 15px 25px;
  &__col {
    display: flex;
    flex-direction: column;
    &:first-child {
      text-align: left;
    }
    &:last-child {
      text-align: right;
    }
  }
  &__title {
    color: #ffffff;
    margin-bottom: 10px;
  }
  &__result {
    font-size: 1.8rem;
    font-weight: 700;
    color: $accent-color;
  }
}